/** @ngInject */
function routesConfig($stateProvider, $urlRouterProvider, $locationProvider) {
  $locationProvider.html5Mode(false).hashPrefix('!');
  $urlRouterProvider.otherwise('/');

  $stateProvider
    .state('home', {
      url: '/',
      component: 'home'
    })
    .state('programs', {
      url: '/programs',
      component: 'programs'
    })
    .state('program', {
      url: '/program/:id',
      component: 'program'
    })
    .state('timer', {
      url: '/timer',
      component: 'timer'
    })
    .state('createProgram', {
      url: '/createProgram?id',
      component: 'createProgram'
    })
    .state('login', {
      url: '/login',
      component: 'login'
    });
}
